.App {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.website-title {
  text-align: center;
  padding: 20px;
}

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: '';
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.toggle-wrapper {
  position: fixed;
  display: flex;
  align-items: center;
  bottom: 10px;
  right: 10px;
}

.toggle-label {
  margin-right: 10px;
}

button {
  padding: 10px;
  background-color: aliceblue;
  border: 1px solid #999;
  border-radius: 5px;
}

button:hover {
  background-color: rgb(197, 205, 211);
  cursor: pointer;
}

.load-clear-container > button {
  margin: 0 5px;
}
